
  @import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,300;0,500;1,700&display=swap');

  *{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family:'Outfit', sans-serif;
    
  }
body{
  background-color: #000;
}
h1,h4,p,span,
a{
  color: #fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color:#222 ;
  border: 1px solid #fff;
  font-weight:600 ;
  cursor: pointer;
}
.btn-light{
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  padding: 12px 32px;
}
.btn:hover{
  background:rgba(255, 255, 255, 0.5);
  color: #fff;
  transition: 0.2s;
}